*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-image: linear-gradient(to left, #acb6e5, #86fde8);
}
.container{
 padding: 20px;
text-align: center;
 display: flex;
 flex-direction: column;
 align-items: center;
justify-content: center;

}

.container p{
  width: 60%;
  color: rgb(82, 75, 75);
}

.container h1{
  font-family: 'Black Ops One', cursive;
  font-size: 3.5rem;
  text-align: center;
  background-clip: text;
  color: transparent;
  background-image: linear-gradient(to left, #ED213A,#93291E);
  animation: animate 6s infinite;

}

.container .text-content{
  margin: 20px;
 width: 80%;
  min-height: 60vh;
  padding: 20px;
  text-align: left;
  font-size: 1.3rem;
  background-color: white;
  box-shadow: 2px 2px 20px 5px rgba(0,0,0,0.1);
  
}

.btn-style{

  width: 60%;
  display: flex;
  justify-content: space-around;
 
}

.btn-style button{
  padding: 10px 30px;
  margin: 5px;
  color: white;
  font-weight: 600;
  font-size: 1.4rem;
  background-color: rgba(30, 143, 255, 0.822);
  border: none;
  border-radius: 5px;
  text-shadow: 2px 2px 20px rgba(0,0,0,0.5);
  cursor: pointer;
}

.btn-style button:nth-child(3){
  background-color: rgba(240, 77, 55, 0.877);

}

.btn-style button:hover{
  background-color: dodgerblue ;
}

.btn-style button:nth-child(3):hover{
  background-color: rgb(240, 77, 55);

}

@keyframes animate{
  to{filter: hue-rotate(300deg);}
}

@media screen and (max-width:600px){
  html{
    font-size: 13px;
  }

  .container p{
    width: 100%;
  }

  .container .text-content{
    width: 100%;
    font-size: 1.3rem;
  }

  .btn-style{
    width: 100%;
  }

  .btn-style button{
    font-size: 1rem;
  }
}

